/*-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/
/* MIXINS -=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=*/

// font-size: calc(24px + 16 * ((100vw - 320px) / (1280 - 320)));

$maxWidth: 1366;
@mixin adaptiv-font($pcSize, $mobSize) {
  $addSize: $pcSize - $mobSize;
  $maxWidth: $maxWidth - 320;
  font-size: calc(
    #{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth})
  );
}

/*-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/

* {
  font-family: "Avenir Next Cyr", sans-serif;
  box-sizing: border-box;
  outline: 0 !important;
}

html {
  overflow-y: scroll !important;
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

p,
h1,
h2,
ul {
  padding: 0;
  margin: 0;
}

/*-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/
/* PRELOADER -=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=*/

.visible {
  visibility: visible;
  opacity: 1;
  transition: opacity 2s linear;
}

.hidden {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 2s, opacity 2s linear;
}

.preloader {
  z-index: 1000;
  position: absolute;
  position: fixed;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  background: linear-gradient(to right, #5f2c82, #49a09d);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: visible;

  .spinner {
    width: 60px;
    height: 60px;
    margin: 100px auto;
    background-color: #fff;

    border-radius: 100%;
    -webkit-animation: sk-scaleout 1s infinite ease-in-out;
    animation: sk-scaleout 1s infinite ease-in-out;
  }

  @-webkit-keyframes sk-scaleout {
    0% {
      -webkit-transform: scale(0);
    }
    100% {
      -webkit-transform: scale(1);
      opacity: 0;
    }
  }

  @keyframes sk-scaleout {
    0% {
      -webkit-transform: scale(0);
      transform: scale(0);
    }
    100% {
      -webkit-transform: scale(1);
      transform: scale(1);
      opacity: 0;
    }
  }
}

/*-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/
/* MENU -=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/

.overlay {
  position: fixed;
  background: linear-gradient(to right, #5f2c82, #49a09d);
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transform-origin: top right;
  transform: scale(0);
  visibility: hidden;
  transition: all 0.4s ease-in-out;
  overflow: hidden;
  z-index: 102;

  &.open {
    opacity: 0.99;
    visibility: visible;
    transform: scale(1);
    li {
      display: block;
      animation: fadeInRight 0.4s cubic-bezier(0.42, 0.83, 0.7, 1.5) forwards;
      animation-delay: 0.4s;
      transform: rotate(-7deg);
      transform-origin: right bottom;
      &:nth-of-type(2) {
        animation-delay: 0.5s;
      }
      &:nth-of-type(3) {
        animation-delay: 0.6s;
      }
      &:nth-of-type(4) {
        animation-delay: 0.7s;
      }
      &:nth-of-type(5) {
        animation-delay: 0.8s;
      }
    }
  }

  .overlay-figure {
    border: 1px solid #fff;
    box-shadow: 0 10px 27px rgba(255, 255, 255, 0.19);
    width: 25%;
    height: 300px;
    border-radius: 40% 60% 60% 40% / 70% 30% 70% 30%;
    animation: morph 20s linear infinite alternate;
    position: absolute;
    top: 50%;
    left: 30%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;

    .email {
      @include adaptiv-font(19, 16);
      color: #fff;
    }
  }
}

.overlay-Menu {
  position: absolute;
  text-align: right;
  width: 100%;
  right: 0%;
  top: 50%;
  transform: translate(-10%, -50%);
  ul {
    list-style: none;
    padding: 0;
    margin: 0 auto;
    display: block;
    position: relative;
    height: 100%;
    li {
      display: block;
      position: relative;
      opacity: 0;
      width: 100%;
      a {
        display: inline-block;
        border: 1px solid rgba(255, 255, 255, 0.09);
        color: #fff;
        text-decoration: none;
        @include adaptiv-font(30, 25);
        padding: 10px 40px 10px 40px;
        margin: 5px 0;
        transition: 0.2s;
        &:hover,
        &:active,
        &:focus {
          transform: translateX(-2px);
          padding-left: 80px;
          outline: 1px solid rgba(255, 255, 255, 0.3);
          background: rgba(255, 255, 255, 0.03);
        }
      }
    }
  }
}

.locked {
  overflow: hidden;
}

@keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 50px;
  }
  100% {
    opacity: 1;
    left: 0;
    transform: rotate(0deg);
  }
}

/*-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/
/* SCROLBAR -=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/

::-webkit-scrollbar-button {
  background-image: url("");
  background-repeat: no-repeat;
  width: 6px;
  height: 0px;
}

::-webkit-scrollbar-track {
  background-color: #2d3436;
  -webkit-box-shadow: 0px 0px 3px #000 inset;
  box-shadow: 0px 0px 3px #000 inset;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #fff;
  -webkit-box-shadow: 0px 1px 1px #b2bec3 inset;
  box-shadow: 0px 1px 1px #b2bec3 inset;
  background-image: url("");
  background-position: center;
  background-repeat: no-repeat;
}

::-webkit-resizer {
  background-image: url("");
  background-repeat: no-repeat;
  max-width: 3px;
  height: 0px;
}

::-webkit-scrollbar {
  width: 4px;
}

/*-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/
/* GLOBAL ELEMENTS -=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=*/

.title-block_media {
}

.title-block {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;

  .title {
    text-align: center;
    text-transform: uppercase;
    @include adaptiv-font(37, 22);
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;

    &:before,
    &:after {
      content: "";
      border: 1px solid #373e42;
      width: 25px;
    }

    &:before {
      margin-right: 15px;
    }

    &:after {
      margin-left: 15px;
    }
  }
}

.content {
  width: 100%;
  padding: 0px 20px;
  display: flex;
  align-items: center;
}

.button {
  border: 1px solid #373e42;
  border-radius: 5px;
  background-color: #fff;
  padding: 10px 20px;
  box-shadow: 0px 0px 49px -8px rgba(0, 0, 0, 0.75);
  transition: 0.3s all;

  &:hover {
    cursor: pointer;
    border: 1px solid #373e42;
    background-color: #373e42;
    color: #fff;
    box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.75);
    transition: 0.3s all;
  }
}

/*-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/
/* HEADER -=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/

.header {
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  overflow: hidden;
  color: #fff;

  .menu-fixed {
    position: fixed;
    width: 100%;
    margin: 0px !important;
    padding: 10px 20px 10px 20px;
    display: flex;
    align-items: center;
    z-index: 1001;
    transition: 0.5s all;
  }

  .header__top {
    margin: 20px 20px 0px 20px;
    display: flex;
    align-items: center;

    .header__icon-block {
      z-index: 1001;

      .header__ico_margin-left {
        margin-left: 15px;
      }

      .header__ico_fixed {
        color: #373e42 !important;

        &:hover {
          color: #000 !important;
          transition: 0.3s all;
        }
      }

      .open-menu-color {
        color: #fff !important;
      }

      .header__ico {
        color: #fff;
        @include adaptiv-font(32, 28);
        transition: 0.3s all;

        &:hover {
          color: #8ca6db;
          transition: 0.3s all;
        }
      }
    }

    /*  COLORS
    ========================================= */

    $bg-color: #2c3e50;
    $white: #ffffff;

    /*  MIXINS
    ========================================= */

    @mixin animation($animation) {
      -webkit-animation: #{$animation};
      -moz-animation: #{$animation};
      -o-animation: #{$animation};
      animation: #{$animation};
    }

    @mixin animation-delay($animation-delay) {
      -webkit-transition-delay: #{$animation-delay};
      -moz-transition-delay: #{$animation-delay};
      -o-transition-delay: #{$animation-delay};
      transition-delay: #{$animation-delay};
    }

    @mixin keyframes($keyframes) {
      @-webkit-keyframes #{$keyframes} {
        @content;
      }
      @-moz-keyframes #{$keyframes} {
        @content;
      }
      @-o-keyframes #{$keyframes} {
        @content;
      }
      @keyframes #{$keyframes} {
        @content;
      }
    }

    @mixin transform($transform) {
      -webkit-transform: $transform;
      -moz-transform: $transform;
      -ms-transform: $transform;
      -o-transform: $transform;
      transform: $transform;
    }

    @mixin transition($transition...) {
      -webkit-transition: #{$transition};
      -moz-transition: #{$transition};
      -o-transition: #{$transition};
      transition: #{$transition};
    }

    @mixin transition-delay($transition-delay) {
      -webkit-transition-delay: #{$transition-delay};
      -moz-transition-delay: #{$transition-delay};
      -o-transition-delay: #{$transition-delay};
      transition-delay: #{$transition-delay};
    }

    /*  MENU ICON
    ========================================= */
    .header__icon-menu {
      position: relative;
      margin-left: auto;
      width: 54px;
      height: 57px;
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 1001;

      .menu-icon {
        position: absolute;
        width: 50px;
        height: 50px;
        cursor: pointer;

        &:hover .dot_fixed {
          background-color: #000 !important;
        }

        .dot_fixed {
          background-color: #373e42 !important;
        }

        .open-menu-color-dot {
          background-color: #fff !important;
        }

        .dot {
          position: absolute;
          top: 50%;
          left: 50%;
          width: 10px;
          height: 10px;
          background-color: $white;
          border-radius: 10px;
          @include transform(translate(-50%, -50%));
          @include transition(margin 0.4s ease 0.4s, width 0.4s ease);

          &:nth-of-type(1) {
            margin-top: -17px;
            margin-left: -17px;
            @include transform(translate(-50%, -50%) rotate(45deg));
          }

          &:nth-of-type(2) {
            margin-top: -17px;
            @include transform(translate(-50%, -50%) rotate(-45deg));
          }

          &:nth-of-type(3) {
            margin-top: -17px;
            margin-left: 17px;
          }

          &:nth-of-type(4) {
            margin-left: -17px;
          }

          &:nth-of-type(5) {
          }

          &:nth-of-type(6) {
            margin-left: 17px;
          }

          &:nth-of-type(7) {
            margin-top: 17px;
            margin-left: -17px;
          }

          &:nth-of-type(8) {
            margin-top: 17px;
          }

          &:nth-of-type(9) {
            margin-top: 17px;
            margin-left: 17px;
          }
        }

        &.clicked {
          .dot {
            @include transition(margin 0.4s ease, width 0.4s ease 0.4s);
            margin-left: 0;
            margin-top: 0;

            &:nth-of-type(1) {
              width: 50px;
            }

            &:nth-of-type(2) {
              width: 50px;
            }
          }
        }
      }
    }
  }

  .header__center {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    .header__image-block {
      width: 12%;
      text-align: center;

      .header__image {
        width: 100%;
        height: 100%;
        border-radius: 100px;
      }
    }

    .header__name-block {
      width: 100%;
      text-align: center;
      margin-top: 20px;

      .header__name {
        @include adaptiv-font(27, 17);
      }
    }

    .header__description-block {
      width: 100%;
      text-align: center;
      margin-top: 10px;

      .header__description {
        font-weight: 100;
        margin: 0;
        padding: 0;
        @include adaptiv-font(20, 14);
      }
    }
  }

  .header__bottom {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;

    &:before,
    &:after {
      display: block;
      content: "";
      width: 50%;
      height: 100%;
      position: absolute;
      top: 0;
      z-index: 1;
    }

    &:before {
      left: 0;
      background-image: -webkit-gradient(
        linear,
        left bottom,
        right top,
        from(#fff),
        color-stop(50%, #fff),
        color-stop(50.5%, transparent),
        color-stop(50.5%, transparent),
        to(transparent)
      );
      background-image: -webkit-linear-gradient(
        bottom left,
        #fff 0%,
        #fff 50%,
        transparent 50.5%,
        transparent 50.5%,
        transparent 100%
      );
      background-image: -o-linear-gradient(
        bottom left,
        #fff 0%,
        #fff 50%,
        transparent 50.5%,
        transparent 50.5%,
        transparent 100%
      );
      background-image: linear-gradient(
        to top right,
        #fff 0%,
        #fff 50%,
        transparent 50.5%,
        transparent 50.5%,
        transparent 100%
      );
    }

    &:after {
      right: 0;
      background-image: -webkit-gradient(
        linear,
        right bottom,
        left top,
        from(#fff),
        color-stop(50%, #fff),
        color-stop(50.5%, transparent),
        color-stop(50.5%, transparent),
        to(transparent)
      );
      background-image: -webkit-linear-gradient(
        bottom right,
        #fff 0%,
        #fff 50%,
        transparent 50.5%,
        transparent 50.5%,
        transparent 100%
      );
      background-image: -o-linear-gradient(
        bottom right,
        #fff 0%,
        #fff 50%,
        transparent 50.5%,
        transparent 50.5%,
        transparent 100%
      );
      background-image: linear-gradient(
        to top left,
        #fff 0%,
        #fff 50%,
        transparent 50.5%,
        transparent 50.5%,
        transparent 100%
      );
    }

    .header__ico-down {
      @include adaptiv-font(30, 26);
      animation: arrow__down-scroll 1.4s linear infinite;
      z-index: 2;
      color: #fff;
      transition: 0.3s all;

      &:hover {
        color: #8ca6db;
        transition: 0.3s all;
      }
    }

    @keyframes arrow__down-scroll {
      0%,
      100% {
        -webkit-transform: translateY(0px);
        -ms-transform: translateY(0px);
        transform: translateY(0px);
      }

      20% {
        -webkit-transform: translateY(2px);
        -ms-transform: translateY(2px);
        transform: translateY(2px);
      }

      40% {
        -webkit-transform: translateY(6px);
        -ms-transform: translateY(6px);
        transform: translateY(6px);
      }

      60% {
        -webkit-transform: translateY(10px);
        -ms-transform: translateY(10px);
        transform: translateY(10px);
      }
    }
  }
}

/*-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/
/* ABOUT ME -=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/

.about-me {
  width: 100%;
  min-height: 100vh;
  background-color: #fff;
  overflow: hidden;
  position: relative;
  color: #373e42;
  display: grid;
  grid-template-rows: 100px 1fr;
  grid-template-columns: 1fr;

  .who-am-i_media-figure {
  }

  .who-am-i_media-text {
  }

  .who-am-i {
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    box-sizing: border-box;

    .who-am-i__title-i {
      width: 100%;
      text-align: left;
      text-transform: uppercase;
      @include adaptiv-font(24, 17);
      font-weight: 600;
      position: relative;

      &:after {
        content: "";
        border: 1px solid #373e42;
        width: 25px;
        margin-left: 30px;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }

    .who-am-i__text-block {
      margin-top: 30px;
      width: 80%;

      .who-am-i__text {
        letter-spacing: 2px;
        line-height: 1.5;
        @include adaptiv-font(17, 15);
      }

      .button-block {
        margin-top: 30px;
      }
    }

    .who-am-i__figure {
      width: 60%;
      height: 350px;
      background: linear-gradient(to right, #5f2c82, #49a09d);
      border-radius: 40% 60% 60% 40% / 70% 30% 70% 30%;
      animation: morph 20s linear infinite alternate;
      box-shadow: 0px 0px 89px 11px rgba(0, 0, 0, 0.75);
      position: relative;
      transition: 0.5s all;

      &:hover {
        width: 65%;
        height: 400px;
        box-shadow: 0px 0px 89px -33px rgba(0, 0, 0, 0.75);
        transition: 0.5s all;
      }

      .who-am-i__icon-block {
        position: absolute;
        top: 50%;
        left: 50%;
        margin-right: -50%;
        transform: translate(-50%, -50%);

        .who-am-i__ico_margin-left {
          margin-left: 15px;
        }

        .who-am-i__ico {
          color: #fff;
          @include adaptiv-font(32, 28);
          transition: 0.3s all;

          &:hover {
            color: #8ca6db;
            transition: 0.3s all;
          }
        }
      }
    }

    @keyframes morph {
      0% {
        border-radius: 40% 60% 60% 40% / 70% 30% 70% 30%;
      }

      20% {
        border-radius: 40% 60% 63% 37% / 39% 52% 48% 61%;
      }

      50% {
        border-radius: 26% 74% 45% 55% / 59% 62% 38% 41%;
      }

      70% {
        border-radius: 47% 53% 27% 73% / 75% 42% 58% 25%;
      }

      100% {
        border-radius: 40% 60%;
      }
    }
  }

  .fadeinleft {
    right: 0% !important;
    transition: 0.5s all;
  }

  .fadeoutright {
    right: -30% !important;
    transition: 0.5s all;
  }

  .modal-window-block {
    position: absolute;
    top: 0;
    right: -40%;
    bottom: 0;
    width: 25%;
    background-color: #fff;
    // z-index: -1;
    box-shadow: 0px 0px 142px -4px rgba(0, 0, 0, 0.75);
    transition: 0.5s all;

    .modal-window {
      width: 100%;
      height: 100vh;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;

      .close-block {
        position: absolute;
        left: -10%;
        background-color: #fff;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0px 5px;
        transition: 0.3s all;

        &:hover {
          cursor: pointer;
          background-color: transparent;
          color: #fff;
          transition: 0.3s all;
        }

        // &:hover .close {
        //   background-color: transparent;
        // }

        .close {
          font-size: 25px;
          cursor: pointer;
        }
      }

      .full-stack-block {
        width: 90%;

        .full-stack-title-block {
          width: 100%;
          text-align: center;

          .full-stack-title {
            font-size: 1.5em;
            @include adaptiv-font(24, 17);
          }
        }

        .full-stack__text {
          margin-top: 10px;

          .full-stack__ul {
            .full-stack__li {
              list-style: none;
              margin-top: 20px;
              line-height: 1.7;
              @include adaptiv-font(17, 15);
            }
          }
        }
      }
    }
  }
}

/*-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/
/* my works -=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/

.my-works {
  width: 100%;
  min-height: 100vh;
  background-color: #fff;
  overflow: hidden;
  position: relative;
  display: grid;
  grid-template-rows: 100px 1fr;
  grid-template-columns: 1fr;
  color: #373e42;

  .my-works__wrapper_media {
    display: none !important;
  }

  .my-works__wrapper {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .my-works__wrapper-block {
      width: 30%;
      display: flex;
      justify-content: center;
      align-items: center;

      .my-works__block {
        background-color: #fff;
        border-radius: 10px;
        box-shadow: 0 10px 27px rgba(0, 0, 0, 0.19);
        width: 100%;
        transition: 0.5s all;

        &:hover {
          box-shadow: 0 10px 27px rgba(0, 0, 0, 0);
          transition: 0.5s all;
        }

        &:hover .my-works__arrow {
          animation: arrow-right 0.9s linear infinite;
          color: #000 !important;
          transition: 0.5s all;
        }

        .my-works__wrapper-img {
          width: 100%;

          .my-works__img {
            width: 100%;
            height: auto;
          }
        }

        .my-works__wrapper-arrow {
          width: 100%;
          margin-top: 10px;
          text-align: right;
          padding: 10px 0px;
          box-sizing: border-box;

          .my-works__arrow {
            color: #373e42;
            font-size: 22px;
            margin-right: 20px;
            transition: 0.5s all;
          }

          @keyframes arrow-right {
            0%,
            100% {
              -webkit-transform: translateX(0px);
              -ms-transform: translateX(0px);
              transform: translateX(0px);
            }

            20% {
              -webkit-transform: translateX(2px);
              -ms-transform: translateX(2px);
              transform: translateX(2px);
            }

            40% {
              -webkit-transform: translateX(6px);
              -ms-transform: translateX(6px);
              transform: translateX(6px);
            }

            60% {
              -webkit-transform: translateX(10px);
              -ms-transform: translateX(10px);
              transform: translateX(10px);
            }
          }
        }
      }
    }
  }
}

/*-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/
/* my contact -=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/

.my-contact {
  width: 100%;
  min-height: 100vh;
  background-color: #fff;
  overflow: hidden;
  position: relative;
  display: grid;
  grid-template-rows: 100px 1fr;
  grid-template-columns: 1fr;
  color: #373e42;

  .my-contact__wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .my-contact__block {
      background-color: #fff;
      position: relative;
      box-shadow: 0 10px 27px rgba(0, 0, 0, 0.19);
      width: 35%;
      height: 350px;
      border-radius: 40% 60% 60% 40% / 70% 30% 70% 30%;
      animation: morph 20s linear infinite alternate;

      // .my-contact__wrapper-title {

      //   .my-contact__title {
      //     text-transform: uppercase;
      //     letter-spacing: 5px;
      //   }
      // }

      .form {
        width: 50%;
        position: absolute;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        top: 50%;
        left: 50%;
        margin-right: -50%;
        transform: translate(-50%, -50%);

        .margin-top {
          margin-top: 20px;
        }

        .input {
          width: 100%;
          border: 0px solid transparent;
          border-bottom: 1px solid #373e42;
          font-size: 0.9em;
          padding: 5px 0px;
          transition: 0.3s all;

          &:hover {
            border-bottom: 1px solid #000;
            transition: 0.3s all;
          }
        }

        .textarea {
          width: 100%;
          height: 100px;
          border: 1px solid #373e42;
          font-size: 0.9em;
          padding: 5px;
          transition: 0.3s all;

          &:hover {
            border: 1px solid #000;
            transition: 0.3s all;
          }
        }

        .button_form {
          margin-top: 30px;
          font-size: 0.9em;
        }
      }
    }
  }
}

/*-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/
